import Vue from 'vue'
import Router from 'vue-router'
import Beautiful from '@/components/Beautiful'
/* import LetItSnow from 'vue-let-it-snow' */
Vue.use(Router)
/* vue.use(LetItSnow); */

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Beautiful',
      component: Beautiful
    }
  ]
})



